<template>
  <div>{{ dataParams.noDataMessage }}</div>
</template>
<script>
  import Vue from 'vue'
  export default Vue.extend({
    data() {
      return {
        dataParams: {
          noDataMessage: '',
        },
      }
    },
    created() {
      this.dataParams = this.params
      this.$vuntangle.vm.$on('no-rows-overlay-component-params', params => (this.dataParams = params))
    },
  })
</script>
