<template>
  <v-dialog
    v-model="displayDialog"
    :transition="false"
    persistent
    :width="width || 600"
    @keydown.esc="onClose"
    @keydown.enter="onAction"
  >
    <v-card>
      <v-card-title class="text-h5 font-weight-light">
        <span v-html="title"></span>
        <v-spacer />
        <v-btn :small="false" icon color="grey" @click="onClose"><v-icon>mdi-close</v-icon></v-btn>
      </v-card-title>
      <!-- the component being rendered inside dialog with the props passed to it -->
      <div v-if="component" :style="`max-height: ${height || 400}px; overflow-y: auto`" class="px-6">
        <component
          :is="component"
          ref="content"
          v-bind="componentProps"
          v-on="componentEvents"
          @progress-show="progress = true"
          @progress-hide="progress = false"
          @close="onClose"
        />
      </div>
      <v-card-actions class="pa-6">
        <v-spacer />
        <template v-if="buttons">
          <u-btn
            v-for="button in buttons"
            :key="button.name"
            :text="button.text"
            @click="onButtonClick(button.handler)"
          >
            {{ button.name }}
          </u-btn>
        </template>
        <template v-else>
          <u-btn :small="false" color="default" :min-width="80" @click="onClose" @keydown.enter.stop>
            {{ cancelLabel || $t('cancel') }}
          </u-btn>
          <u-btn :small="false" :min-width="80" elevation="0" @click="onAction" @keydown.enter.stop>
            {{ actionLabel || $t('ok') }}
          </u-btn>
        </template>
      </v-card-actions>
      <v-overlay v-if="progress" absolute>
        <v-progress-circular indeterminate color="primary" />
      </v-overlay>
    </v-card>
  </v-dialog>
</template>

<script>
  import {
    VDialog,
    VCard,
    VCardTitle,
    VSpacer,
    VBtn,
    VIcon,
    VCardActions,
    VOverlay,
    VProgressCircular,
  } from 'vuetify/lib'

  export default {
    components: { VDialog, VCard, VCardTitle, VSpacer, VBtn, VIcon, VCardActions, VOverlay, VProgressCircular },
    data() {
      return {
        title: null,
        component: null,
        componentProps: null,
        componentEvents: null,
        buttons: null,
        actionLabel: null,
        cancelLabel: null,
        width: null,
        height: null,
        progress: false,
      }
    },
    computed: {
      // returns a boolean showing/hiding the dialog if component is set
      displayDialog() {
        return this.component !== null
      },
    },
    created() {
      this.$vuntangle.vm.$on('show-dialog', this.show)
    },

    methods: {
      show(options) {
        Object.assign(this, { ...options })
      },
      /**
       * Hides and resets the dialog data
       */
      onClose() {
        this.$emit('close') // this notifies dialog opener about closing

        this.title = null
        this.component = null
        this.componentProps = null
        this.buttons = null
        this.cancelLabel = null
        this.actionLabel = null
        this.width = null
        this.progress = false

        this.$off('close')
      },

      onAction() {
        // the inner content component must implement "action" method
        this.$refs.content.action()
      },
      onButtonClick(handler) {
        // bind 'this' to this component, can use arrow function to get the caller 'this'
        const bindThis = handler.bind(this)
        bindThis()
      },
    },
  }
</script>
