import { VCheckbox } from 'vuetify/lib'

export default {
  extends: VCheckbox,
  computed: {
    computedColor() {
      if (!this.isActive) return undefined
      if (this.color) return this.color
      if (this.isDark && !this.appIsDark) return 'white'
      return 'aristaMediumBlue'
    },
  },
}
